.expandable-element {
  $base: &;
  @apply flex items-start;

  &__content {
    @apply overflow-hidden transition-all;
  }

  &__control {
    @apply cursor-pointer transition-all;
    @apply flex items-center;
    svg {
      @apply scale-75;
    }
  }

  &--expanded {
    #{$base}__content {
      @apply h-auto pb-sm;
    }

    #{$base}__control {
      @apply rotate-180;
    }
  }

  &--collapsed {
    #{$base}__content {
      @apply h-none py-none;
    }
  }
}