.expanded-text {
  @apply relative;
  @apply font-inter;

  .truncate-lines {
    @apply overflow-hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
    position: relative;
  }
  
  .read-more {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
    @apply absolute right-none bottom-none;
    @apply font-semibold text-on-tertiary-container;
    @apply cursor-pointer;
    @apply pl-xs+7;
    background: linear-gradient(90deg, rgba(246, 244, 241, 0) 0%, rgba(246, 244, 241, 1) 12%);
  }
}