.nav-bar {
  $base: &;
  @apply z-20;
  @apply bg-background;
  @apply fixed top-none left-none right-none;
  @apply border-b border-outline-variant;

  &__container {
    @apply flex justify-between items-center;
    @apply h-4xl px-xs+4;
  }

  &__middle {
    @apply flex items-center;

    .title {
      @apply font-inter font-semibold text-body-lg text-on-surface;
    }
  }

  &#{$base}__transparent {
    @apply bg-transparent sm:bg-background;
    @apply border-none sm:border-solid;
    @apply sm:border-b sm:border-outline-variant;
    
    #{$base}__left, #{$base}__right {
      @apply shadow-evaluation-3 focus:shadow-evaluation-3 bg-background;
      @apply sm:shadow-none sm:focus:shadow-none sm:bg-transparent;
    }

    #{$base}__middle {
      @apply hidden sm:flex;
    }
  }
}
