.slider {
  .slider-header {
    @apply flex items-center;
  }

  .slider-navigation-top-right {
    @apply flex justify-end;
    @apply w-100%;
  }

  .slider-navigation-top-left {
    @apply flex items-center;
    @apply gap-sm;
  }

  .slider-navigation-top-center {
    @apply flex items-center justify-center;
    @apply w-100%;
  }

  .slider-navigation-bottom-right {
    @apply flex justify-end;
  }

  .slider-navigation-bottom-center {
    @apply flex justify-center;
  }

  .slider-navigation-top-full-w, .slider-navigation-bottom-full-w {
    @apply w-100%;
    .navigation {
      @apply justify-between;
    }
  }

  .slider-navigation-bottom {
    @apply -mt-xs;
  }

  .elements-list-wrapper {
    @apply relative;
    @apply my-xs;
  }

  .elements-list {
    @apply flex;
    @apply gap-sm;
    @apply overflow-x-hidden;
    @apply pb-xs;
  }

  .fade-out-left, .fade-out-right {
    @apply absolute;
    @apply bg-surface+2;
    @apply top-none;
    @apply min-h-100%;
    @apply w-lg;
  }

  .fade-out-left {
    @apply left-none;
    mask-image: linear-gradient(to right, black 0%, transparent 100%);
  }

  .fade-out-right {
    @apply right-none;
    mask-image: linear-gradient(to left, black 0%, transparent 100%);
  }
}
