.location-preview {
  $base: &;
  @apply rounded-16px;
  @apply overflow-hidden;
  @apply relative;
  @apply transition-shadow;

 &__content {
  position:absolute;
  top:0;
  bottom:0;
  left:0;
  right:0;
 }

  &__image {
    @apply w-100% h-100%;

    &:before {
      content: '';
      background: linear-gradient(180deg, rgba(17, 23, 32, 0) 0%, rgba(17, 23, 32, 0.7) 100%);
      @apply block absolute top-none w-100% h-100% z-10;
    }
  }

  &__info {
    @apply w-100% min-h-6xl;
    @apply absolute bottom-none z-10;
    @apply flex items-center justify-between;
    @apply p-sm; 
  }

  &__logo {
    @apply mr-xs;
  }

  &__main-info {
    @apply overflow-hidden;
    @apply flex flex-2 flex-col;
    @apply text-on-secondary;
    @apply font-optima text-xs sm:font-inter sm:text-body-sm font-semibold sm:font-normal;

    span {
      @apply whitespace-nowrap overflow-hidden text-ellipsis;
    }
  } 

  html:lang(vi) & {
    #{$base}__main-info {
      @apply font-inter;
    }
  }

  &__rating {
    @apply ml-sm+2;
    @apply text-body-sm;
    @apply self-end items-center;
    @apply flex whitespace-nowrap;
    @apply text-on-secondary;

    svg {
      @apply mr-none+3 mb-none+4 scale-75;
      @apply fill-on-secondary;
    }
  }

  &:hover {
    @apply cursor-pointer;
    box-shadow: 0px 6px 10px 4px rgba(48, 47, 46, 0.15);
    filter: drop-shadow(0px 2px 3px rgba(48, 47, 46, 0.3));
  }

  &:active {
    box-shadow: none;
    filter: none;
  }
}
