.amenities {
  .amenities-list {
    @apply flex;
    @apply w-100%;
    @apply flex-col;
    @apply lg:flex-row;
    @apply lg:flex-wrap;
    @apply gap-md;
    @apply my-sm;
    @apply font-inter text-2xs text-on-surface;

    .amenity {
      @apply flex;
      @apply items-center;
      @apply gap-xs+2;
      @apply lg:w-30%;
    }
  }
}
