.artist-preview {
  @apply min-w-140px w-140px h-180px;
  @apply bg-surface+3;
  @apply border rounded-8px border-surface-variant;
  @apply flex flex-col items-center justify-between;
  @apply p-sm;
  @apply transition-shadow;

  .logo {
    @apply relative;

    .company-logo {
      @apply absolute z-10 -top-none+3 -left-none+3;
      @apply h-md+4 w-md+4 p-none+2;
      @apply rounded-1/2 bg-surface+3;
    }
  }


  .time {
    @apply font-inter text-caption-lg font-normal text-on-surface-variant;
    span {
      @apply block;
    }
  }

  &:hover {
    @apply cursor-pointer;
    @apply shadow-evaluation-1;
  }

  &:active {
    @apply shadow-transparent;
  }
}
