.icon-button {
  @apply flex items-stretch;
  @apply rounded-1/2;
  @apply overflow-hidden;
  @apply transition-colors;
  -webkit-tap-highlight-color: transparent;
  
  &.sm {
    @apply w-xl h-xl;
  }

  &.lg {
    @apply w-2xl+4 h-2xl+4;
  }

  .state-layer {
    @apply flex-1 flex items-center justify-center;
  }
}
