.container {
  min-height: 100vh;
  @apply lg:flex lg:justify-center lg:gap-md lg:px-md lg:py-sm;
  @apply xl:gap-lg;

  @media (min-width: 992px) {
    min-height: calc(100vh - 16px);
  }

  & > div {
    @apply w-100%;
  }

  .left-col {
    @apply lg:max-w-720px;
    @apply overflow-hidden;
  }

  .right-col {
    @apply lg:max-w-375px;
  }
}
