.breadcrumbs {
  @apply flex;
  @apply items-center;
  @apply text-body-sm;
  @apply font-inter;
  @apply h-xl;

  .path {
    @apply fill-on-surface-variant;
  }

  .root {
    @apply font-semibold;
    @apply cursor-pointer;
  }

  .breadcrumb {
    @apply font-normal;
    @apply text-on-surface-variant;
  }

  .breadcrumb-links {
    @apply flex;
    @apply items-center;
    @apply gap-none+2;
  }
}
