.modal-component {
  @apply hidden fixed z-20 top-none left-none right-none bottom-none ;
  @apply overflow-y-scroll;
  @apply lg:absolute lg:top-none lg:left-none lg:right-none lg:bottom-none;
  animation: appear-modal-in 0.3s;
  animation-fill-mode: forwards;

  &--active {
    @apply block;

    .modal-component__container {
      animation: appear-modal-container-mobile-in 0.3s;
      animation-fill-mode: forwards;

      @media (min-width: 576px) {
        animation: appear-modal-container-in 0.3s;
      }
      @media (min-width: 992px) {
        animation: appear-modal-container-mobile-in 0.3s;
      }
    }
  }

  &--inactive {
    @apply block;
    animation: appear-modal-out 0.3s;
    animation-fill-mode: forwards;


    .modal-component__container {
      animation: appear-modal-container-mobile-out 0.3s;
      animation-fill-mode: forwards;

      @media (min-width: 576px) {
        animation: appear-modal-container-out 0.3s;
        animation-fill-mode: forwards;
      }
      @media (min-width: 992px) {
        animation: appear-modal-container-mobile-out 0.3s;
      }
    }
  }

  &__container {
    @apply relative h-100vh pt-lg+4;
    @apply sm:w-375px sm:ml-auto sm:mr-none sm:pt-none;
    @apply md:w-480px;
    @apply lg:w-100% lg:pt-sm;
  }

  &__content {
    @apply pt-none; 
    @apply sm:pt-none;
  }

  &__children {
    @apply -mt-4xl rounded-t-16px overflow-hidden bg-surface+3;
    @apply sm:rounded-none sm:-mt-4xl+4;
    @apply lg:rounded-t-16px;
  }

  &__close-button-container {
    @apply z-10 sticky top-none flex items-center pl-xs+4 py-xs+4;

    &--scrolled {
      @apply bg-surface+3 border-b border-surface-variant transition-all;

      .modal-component__close-button.icon-button.default {
        @apply shadow-none bg-transparent;
      }
    }
  }

  &__close-button.icon-button.default  {
    @apply shadow-evaluation-1 bg-background;
  }
}

@keyframes appear-modal-container-in {
  0% {
    right: -100vw;
  }
  100% {
    right: 0;
  }
}

@keyframes appear-modal-container-out {
  0% {
    right: 0;
  }
  100% {
    right: -100vw;
  }
}

@keyframes appear-modal-container-mobile-in {
  0% {
    top: 100vh;
  }
  100% {
    top: 0;
  }
}

@keyframes appear-modal-container-mobile-out {
  0% {
    top: 0;
  }
  100% {
    top: 100vh;
  }
}

@keyframes appear-modal-in {
  0% {
    background-color: rgba(7, 9, 13, 0);
  }
  100% {
    background-color: rgba(7, 9, 13, 0.6);
  }
}

@keyframes appear-modal-out {
  0% {
    background-color: rgba(7, 9, 13, 0.6);
  }
  100% {
    background-color: rgba(7, 9, 13, 0);
  }
}
